.container {
    padding: 100px 0;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.title {
    margin-bottom: 50px;
    font-size: 18px;
    font-weight: 500;
    color: #333;
}

.projects {
    width: 70%;
    display: grid;
    grid-template-columns: 30% 30% 30%;
    justify-content: space-between;
    row-gap: 50px;
}

.project {
    display: flex;
    flex-direction: column;
    position: relative;
}

.project .banner {
    height: 200px;
    overflow: hidden;
}

.project .tag {
    position: absolute;
    right: 0;
    top: 0;
    color: #f7f7f7;
    font-size: 12px;
    font-weight: 600;
    translate: -10px -50%;
    border-radius: 5px;
    z-index: 1;
    padding: 3px 5px;
}

.bggreen {
    background: #1e9a1e;
}

.bgred {
    background: #c01919;
}

.bgpurple {
    background: #970ba5;
}

.bgorange {
    background: #d58d24;
}

.project .banner img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all 0.3s ease;
}

.project .githubstats {
    display: flex;
    column-gap: 5px;
    position: absolute;
    left: 10px;
    top: 10px;
    z-index: 1;
}

.project .name {
    text-align: center;
    font-size: 16px;
    font-weight: 700;
    background: #0002;
    padding: 10px 0;
}

.project .desc {
    background: #0001;
    padding: 10px;
    font-size: 14px;
    text-align: center;
    flex-grow: 1;
    padding-bottom: 30px;
}

.project .techs {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: center;
    background: #0002;
    padding: 5px 0;
}

.project .techs img {
    width: 30px;
}

.project:hover .banner img {
    scale: 1.1;
    cursor: pointer;
}