.container {
    width: 100%;
    padding: 100px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.title {
    font-size: 18px;
    font-weight: 500;
    color: #333;
}

.section {
    font-size: 18px;
    font-weight: 300;
    margin-top: 30px;
    margin-bottom: 10px;
    color: #777;
    border-bottom: 1px solid #999;
    width: fit-content;
}

.techs {
    display: flex;
    column-gap: 25px;
}

.tech {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;    
    font-size: 16px;
    font-weight: 600;

    transition: all 0.5s ease;
    translate: 100px 0 0;
    opacity: 0;
}

.tech img {
    height: 70px;
}