.container {
    display: flex;
    width: 100%;
    height: 100vh;
    flex-direction: column;    
    align-items: center;
    justify-content: center;
    position: relative;
}

.profile {
    height: 150px;
    width: 150px;
    overflow: hidden;
    border-radius: 50%;
    border: 2px solid #555;
}

.profile img {
    width: 100%;
    transform: translateY(-15px);
}

.name {
    font-size: 28px;
    margin-top: 10px;
    font-weight: 600;
}

.title {
    font-weight: 600;    
}

.social {
    margin-top: 20px;
    display: flex;
    column-gap: 15px;
}

.social img {
    height: 30px;
    transition: all 0.1s ease;
}

.social img:hover {
    transform: scale(1.1);
}

.scrolldown {
    position: absolute;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #999;
    padding-bottom: 10px;
}

.scrolldown .mouse {
    height: 30px;
    width: 20px;
    border: 2px solid #999;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
}

.scrolldown .wheel {
    height: 5px;
    width: 5px;
    background: #999;
    margin-left: 2px;
    border-radius: 50%;
    animation: scrollwheel 2s infinite;
}

@keyframes scrollwheel {
    0% {translate: 0 -5px 0 ;}
    80% {translate: 0 5px 0 ;}
    100% {translate: 0 -5px 0 ;}
  }