.container {
    padding: 100px 0;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.title {
    width: 100%;
    text-align: center;
    margin-bottom: 50px;
    font-size: 18px;
    font-weight: 500;
    color: #333;
}

.dot {
    width: 15px;
    height: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.dot span {
    position: absolute;
    height: 100%;
    width: 100%;
    background: #444;
    border-radius: 50%;
}

.current {
    position: absolute;
    height: 100%;
    width: 100%;
    background: #444;
    border-radius: 50%;
    animation: pulse 1s infinite;
}

@keyframes pulse {
    0% {scale: 1;}
    50% {scale: 1.1;}
    100% {scale: 1;}
}

.dot .year {
    color: #666;
    font-weight: 700;
    font-size: 14px;
    translate: calc(50% + 20px) 0 0;
    white-space: nowrap;
}

.exp {
    width: 100%;
    display: grid;
    grid-template-columns: 50% 0 50%;
    color: #222;
    font-weight: 600;
}

.line {
    border-right: 3px solid #444;
}

.left {
    display: flex;
    justify-content: left;
    padding: 50px 50px;
    flex-direction: column;
    row-gap: 40px;
}

.right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    padding: 50px 50px;
    font-size: 18px;
}

.exp .place {
    font-weight: 700;
    color: #666;
    font-size: 12px;
}
